import { useEffect, useState } from "react";
import "./App.css";
import externalData from "./data.json";
import {
  Box,
  Button,
  ButtonGroup,
  ChakraProvider,
  Drawer,
  DrawerBody,
  DrawerContent,
  DrawerHeader,
  DrawerOverlay,
  Flex,
  useDisclosure,
} from "@chakra-ui/react";
import React from "react";

const App: React.FC = () => {
  return (
    <ChakraProvider>
      <NameComepetition />
    </ChakraProvider>
  );
};

const NameComepetition = () => {
  return <Competition />;
};

const Competition = () => {
  const [data] = useState<Array<string>>([...externalData.names]);
  const [matchUps, setMatchUps] = useState<Array<Array<string>>>([]);
  const [is_loading, set_is_loading] = useState<boolean>(true);
  const [current_index, set_current_index] = useState<number>(0);
  const [winners, set_winners] = useState<Array<string>>([]);
  const [losers, set_losers] = useState<Array<string>>([]);
  const [selection, setSelection] = useState<number | null>(null);
  const btnRef = React.useRef<HTMLButtonElement>(null);
  const { isOpen, onOpen, onClose } = useDisclosure();

  const get_randomized_names = (
    list_of_names: Array<string>,
  ): Array<string> => {
    return list_of_names
      .map((d) => {
        return { data: d, number: Math.random() };
      })
      .sort((a, b) => a["number"] - b["number"])
      .map((d) => d["data"])
      .filter((_, i) => i < 50);
  };

  const get_two_length_arrays = (list_of_names: Array<string>) => {
    let names_in_two = [];
    for (let index = 0; index < list_of_names.length - 1; index += 2) {
      names_in_two.push([list_of_names[index], list_of_names[index + 1]]);
    }
    return names_in_two;
  };

  const set_outcome = (winner_index: number) => {
    if (winner_index === 0) {
      set_winners([...winners, matchUps[current_index][0]]);
      set_losers([...losers, matchUps[current_index][1]]);
    } else {
      set_winners([...winners, matchUps[current_index][1]]);
      set_losers([...losers, matchUps[current_index][0]]);
    }

    if (current_index + 1 === matchUps.length) {
      set_current_index(0);
      setMatchUps(get_two_length_arrays(get_randomized_names(winners)));
      set_winners([]);
    } else {
      set_current_index(current_index + 1);
    }
    setSelection(null);
  };

  const init = () => {
    const temp_data = get_randomized_names(data);
    set_current_index(0);
    setMatchUps(get_two_length_arrays(temp_data));
    set_is_loading(false);
    set_winners([]);
    set_losers([]);
  };

  useEffect(() => {
    if (is_loading) {
      init();
    }
  });

  return (
    <>
      <Flex
        direction="column"
        alignItems="center"
        justifyContent="center"
        height="100vh"
        bg="#282c34"
      >
        <Flex
          direction="column"
          gap={4}
          alignItems="center"
          border="2px solid teal"
          p={4}
          borderRadius="md"
        >
          <Box border="1px solid teal" bg={"teal.50"} p={4} borderRadius="md">
            {matchUps.length > 0 ? (
              <ButtonGroup gap="2">
                <Button
                  colorScheme={"teal"}
                  size={"lg"}
                  isActive={selection === 0}
                  onClick={() => setSelection(0)}
                >
                  {matchUps[current_index][0]}
                </Button>
                <Button
                  colorScheme={"teal"}
                  size={"lg"}
                  isActive={selection === 1}
                  onClick={() => setSelection(1)}
                >
                  {matchUps[current_index][1]}
                </Button>
              </ButtonGroup>
            ) : null}
          </Box>
          <Box>
            <Button
              leftIcon={<span></span>}
              isDisabled={selection === null}
              onClick={() => selection !== null && set_outcome(selection)}
            >
              Confirm
            </Button>
          </Box>
        </Flex>
        <Flex>
          <label>{matchUps.length}</label>
          <Button onClick={() => init()}>Reset</Button>
          <Button ref={btnRef} onClick={onOpen} colorScheme="blue">
            Standings
          </Button>
        </Flex>
      </Flex>
      <Drawer
        isOpen={isOpen}
        placement="right"
        finalFocusRef={btnRef}
        onClose={onClose}
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerHeader>Winners</DrawerHeader>
          <DrawerBody>
            {winners.map((w, i) => (
              <li key={i}>{w}</li>
            ))}
          </DrawerBody>
        </DrawerContent>
      </Drawer>
    </>
  );
};

export default App;
